}
/*自适应布局*/
@media (max-width: 700px) {
    #primary-navigation {
        height: 38px;
        float: none;
        top: 0;
    }
    .navbar {
        position: absolute!important;
    }
    #primary-navigation .nav-menu {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }
    #primary-navigation .menu-item {
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
    }
    #primary-navigation a {
        width: 100%;
        padding: 0;
        text-align: center;
    }
    #primary-navigation ul ul a{
        padding: 14px 12px;
        white-space: normal;
    }
    .title {
      float: none!important;
      margin: 0 auto;
    }
}
@media screen and (min-width: 6px) {
  .primary-navigation {
      font-size: 16px;
  }
  .site-navigation {
      width: 50%;
      float: left;
  }
  .primary-navigation li {
      border: 0;
      display: inline-block;
      height: 38px;
      line-height: 60px;
